/*  Code Highlight Styles   */

@import "fruity.css";
/*  


*/

.material-symbols-outlined {
    cursor: pointer;
    position: relative;
    display: inline-block;
    margin: 1%;
    float: right;
    font-variation-settings:
    'FILL' 0,
    'wght' 400,
    'GRAD' 0,
    'opsz' 48;

}

.material-symbols-outlined .tooltiptext {
    visibility: hidden;
    width: 50px;
    background-color: white;
    color: #C84848;
    font-weight: 800;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    text-align: center;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -20px;
    opacity: 0;
    transition: opacity 0.3s;
    font-size: 12px;
  }
  
.material-symbols-outlined .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-radius: 0px;
    border-color: white transparent transparent transparent;
  }
  
.material-symbols-outlined:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }

